/**
 * Global state manager for the RAG chat application
 */
export class StateManager {
    constructor() {
        this.currentSession = null;
        this.listeners = new Map();
        // Connection state
        this.connectionState = 'disconnected';
        this.initializeSession();
    }
    static getInstance() {
        if (!StateManager.instance) {
            StateManager.instance = new StateManager();
        }
        return StateManager.instance;
    }
    initializeSession() {
        this.currentSession = {
            id: this.generateSessionId(),
            messages: [],
            created_at: new Date(),
            updated_at: new Date()
        };
    }
    generateSessionId() {
        return 'session_' + Date.now().toString(36) + Math.random().toString(36).substr(2);
    }
    // Session management
    getCurrentSession() {
        return this.currentSession;
    }
    createNewSession() {
        this.currentSession = {
            id: this.generateSessionId(),
            messages: [],
            created_at: new Date(),
            updated_at: new Date()
        };
        this.emit('sessionCreated', this.currentSession);
        return this.currentSession;
    }
    // Message management
    addMessage(message) {
        if (!this.currentSession) {
            this.initializeSession();
        }
        const fullMessage = {
            ...message,
            id: this.generateMessageId(),
            timestamp: new Date()
        };
        this.currentSession.messages.push(fullMessage);
        this.currentSession.updated_at = new Date();
        this.emit('messageAdded', fullMessage);
        return fullMessage;
    }
    updateMessage(messageId, updates) {
        if (!this.currentSession)
            return null;
        const messageIndex = this.currentSession.messages.findIndex(m => m.id === messageId);
        if (messageIndex === -1)
            return null;
        const updatedMessage = {
            ...this.currentSession.messages[messageIndex],
            ...updates
        };
        this.currentSession.messages[messageIndex] = updatedMessage;
        this.emit('messageUpdated', updatedMessage);
        return updatedMessage;
    }
    getMessages() {
        return this.currentSession?.messages || [];
    }
    getLastMessage() {
        const messages = this.getMessages();
        return messages.length > 0 ? messages[messages.length - 1] : null;
    }
    generateMessageId() {
        return 'msg_' + Date.now().toString(36) + Math.random().toString(36).substr(2);
    }
    // Citation management
    addCitationsToMessage(messageId, citations) {
        const message = this.updateMessage(messageId, { citations });
        if (message) {
            this.emit('citationsAdded', { messageId, citations });
        }
    }
    getAllCitations() {
        const messages = this.getMessages();
        return messages
            .filter(message => message.citations)
            .flatMap(message => message.citations)
            .filter((citation, index, arr) => arr.findIndex(c => c.id === citation.id) === index); // Remove duplicates
    }
    // Event system
    on(event, listener) {
        if (!this.listeners.has(event)) {
            this.listeners.set(event, new Set());
        }
        this.listeners.get(event).add(listener);
    }
    off(event, listener) {
        const eventListeners = this.listeners.get(event);
        if (eventListeners) {
            eventListeners.delete(listener);
            if (eventListeners.size === 0) {
                this.listeners.delete(event);
            }
        }
    }
    emit(event, data) {
        const eventListeners = this.listeners.get(event);
        if (eventListeners) {
            eventListeners.forEach(listener => {
                try {
                    listener(data);
                }
                catch (error) {
                    console.error(`Error in event listener for ${event}:`, error);
                }
            });
        }
    }
    setConnectionState(state) {
        if (this.connectionState !== state) {
            this.connectionState = state;
            this.emit('connectionStateChanged', state);
        }
    }
    getConnectionState() {
        return this.connectionState;
    }
    // Utility methods
    clearSession() {
        if (this.currentSession) {
            this.currentSession.messages = [];
            this.emit('sessionCleared', this.currentSession);
        }
    }
    exportSession() {
        return JSON.stringify(this.currentSession, null, 2);
    }
    importSession(sessionData) {
        try {
            const session = JSON.parse(sessionData);
            // Basic validation
            if (session && session.id && Array.isArray(session.messages)) {
                this.currentSession = {
                    ...session,
                    created_at: new Date(session.created_at),
                    updated_at: new Date(session.updated_at)
                };
                this.emit('sessionImported', this.currentSession);
                return true;
            }
        }
        catch (error) {
            console.error('Failed to import session:', error);
        }
        return false;
    }
}
